<template>
	<div>
		<h1>Halaman update Form</h1>
		<form @submit.prevent="UpdateProduct">
			<div class="field">
				<label for="nama">Nama</label>
				<div class="control">
					<input type="text" v-model="nama" id="nama" class="input" placeholder="nama">
				</div>
			</div>
			<div class="field">
				<label for="kategori">Kategori</label>
				<div class="control">
					<input type="text" v-model="kategori" id="kategori" class="input" placeholder="Kategori">
				</div>
			</div>
			<div class="field">
				<div class="control">
					<button class="button">Save</button>
				</div>
			</div>
		</form>
	</div>
</template>

<script>
	import axios from 'axios';
	export default{
		name: "EditForm",
		data(){
			return{
				nama:"",
				kategori: "",
			}
		},
		created(){
			this.getProductById();
		},
		methods: {
		async getProductById () {
			try {
			const response = await axios.get(`product/${this.$route.params.id}`);
			this.nama = response.data.nama,
				this.kategori  = response.data.kategori
				}catch(error){
				console.log(error);
				}
			},
		async UpdateProduct(){
			try {
			await axios.put(`product/${this.$route.params.id}`,{
					nama: this.nama,
					kategori: this.kategori
				});
				this.nama = "",
				this.kategori = ""
				this.$router.push('/products');
				}catch(error){
				console.log(error);
				}
		}
		},

	};
</script>

