<template>
	<div>
		<h1>daftar semua product</h1>
		<router-link :to="{name: 'AddProduct'}">Tambah Data</router-link>
		<table>
			<thead>
				<tr>
					<th>Nama</th>
					<th>Kategori</th>
					<th>actions</th>
				</tr>
			</thead>
			<tbody>
				<tr v-for="product in products" :key="product.id">
					<td>{{ product.nama }}</td>
					<td>{{ product.kategori }}</td>
					<td>
						<button><router-link :to="{name: 'EditProduct', params: { id: product.id}}">Edit</router-link></button>
						<button class="button" @click="deleteProduct(product.id)">delete</button>
					</td>
				</tr>
			</tbody>
		</table>
	</div>
</template>

<script>
	import axios from "axios";
	export default {
		name : 'AllProduct',
		data(){
			return{
				products: []
			}
		},
		created(){
			this.getProduct();
		},
		methods: {

			//method ambil semua data
			async getProduct(){
				try {
					const response = await axios.get('product');
					this.products = response.data;
				} catch (error) {
					console.log(error);
				}
			},

			//method delete
			async deleteProduct(id){
				try {
					await axios.delete(`product/${id}`);
					this.getProduct();
				} catch (error) {
					console.log(error);
				}
			}
		}

	}

</script>