<template>
	<div>
		<h1>Halaman Add Form</h1>
		<form @submit.prevent="saveProduct">
			<div class="field">
				<label for="nama">Nama</label>
				<div class="control">
					<input type="text" v-model="nama" id="nama" class="input" placeholder="nama">
				</div>
			</div>
			<div class="field">
				<label for="kategori">Kategori</label>
				<div class="control">
					<input type="text" v-model="kategori" id="kategori" class="input" placeholder="Kategori">
				</div>
			</div>
			<div class="field">
				<div class="control">
					<button class="button">Save</button>
				</div>
			</div>
		</form>
	</div>
</template>

<script>
	import axios from 'axios';
	export default {
		name: "AddForm",
		data(){
			return {
				nama: '',
				kategori: ''
			}
		},
		methods: {
			async saveProduct(){
				try {
					if (!this.nama == '', !this.kategori == '') {
						await axios.post('product', {
							nama: this.nama,
							kategori: this.kategori
						});
					this.nama = '',
					this.kategori = '',
					this.$router.push('/products');
					}
				} catch(error) {
					console.log(error);
				}
			}
		}
	};
</script>

